@media screen and (max-width: 768px) {
  .container {
    padding: 10px;
  }

  .pokemon-card {
    margin: 10px auto;
    width: 90%; /* Adjust width for smaller screens */
  }

  h1 {
    font-size: 20px;
  }

  .button {
    font-size: 14px;
  }
}

